import { Component } from '@angular/core';
import { users } from './users.config';

@Component({
  selector: 'app-users',
  templateUrl: './users.component.html',
  styleUrls: ['./users.component.scss']
})
export class UsersComponent {
  page: string = "data";
  data: users;
  constructor() { }
  openNewUser(a: users) {
    if (a != null)
      this.data = a;
    this.page = "newUser"
  }
  openData() {
    this.data = undefined;
    this.page = "data";
  }
}
