import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { AppService } from 'src/app/app.service';
import { users } from '../users.config';

@Component({
  selector: 'app-data-users',
  templateUrl: './data-users.component.html',
  styleUrls: ['./data-users.component.scss']
})
export class DataUsersComponent implements OnInit {
  @Output() openNew: EventEmitter<users | null> = new EventEmitter();
  users: users[] = [];
  searchText: string;
  constructor(public app: AppService) { }
  ngOnInit() {
    this.getUsers();
  }
  getUsers() {
    this.app.wait();
    this.app.http.get<users[]>("api/users").subscribe(
      res => {
        this.users = res;
        this.app.wait(false);
      }, err => {
        console.log(err);
      }
    )
  }
  deleteUser(user: users) {
    this.app.dialogService.confirm(
      "تاكيد حذف الجلسة",
      "هل انت متاكد من حذف الجلسة",
      "نعم",
      "لا").subscribe(
        res => {
          if (res == true) {
            this.app.http.delete(`/api/users/${user.id}`).subscribe(
              res => {
                this.ngOnInit();
              },
              err => {
                this.app.wait(false);
              }
            )
          }
        }
      )
  }
  goToUser(ele: HTMLInputElement, user: users) {
    if (ele.checked) {
      this.app.dialogService.confirm(
        "تاكيد الانتقال",
        `هل تريد الانتقال الى مستخدم ${user.name}`,
        "نعم",
        "لا"
      ).subscribe(
        res => {
          if (res == true) {
            this.app.user = user;
            this.app.router.navigateByUrl("dashboard");
          } else {
            ele.checked = false;
          }
        }
      )
    }
  }
  showDelete(x: users) {
    if (x.id == this.app.user.id) return false;
    else if (this.app.showUser() && x.type == "admin") return false;
    else return true;
  }
}
