import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { AppService } from 'src/app/app.service';
import { users } from '../users.config';

@Component({
  selector: 'app-new-user',
  templateUrl: './new-user.component.html',
  styleUrls: ['./new-user.component.scss']
})
export class NewUserComponent implements OnInit {
  @Output() openNew: EventEmitter<null> = new EventEmitter();
  @Input("data") data: users;
  userForm: FormGroup;
  constructor(private app: AppService) { }
  ngOnInit() {
    this.userForm = new FormGroup({
      username: new FormControl("", [Validators.required]),
      type: new FormControl("user", [Validators.required]),
      name: new FormControl("", [Validators.required]),
      password: new FormControl("", [Validators.required]),
      id: new FormControl(""),
      img: new FormControl(""),
    });
    if (this.data != undefined) {
      this.userForm.setValue(this.data);
    }
  }
  updateUser() {
    if (!this.userForm.valid) {
      this.app.log("بعض البيانات مفقودة");
      return;
    }
    this.app.wait();
    if (this.data != undefined) {
      this.app.http.put(`api/users/${this.userForm.value.id}`, this.userForm.value).subscribe(
        res => {
          this.app.wait(false);
          this.openNew.emit();
        }, err => { }
      );
    } else {
      this.app.http.post(`api/users`, this.userForm.value).subscribe(
        res => {
          this.app.wait(false);
          this.openNew.emit();
        }, err => { }
      );
    }
  }
}
